"use client"

import { useState, useEffect } from "react"
import { TrainingEvent } from "@/types"
import TrainingPlanForm from "@/components/TrainingPlanForm"
import { Calendar } from "@/components/ui/calendar"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"

export default function CalendarPage() {
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date())
  const [trainingEvents, setTrainingEvents] = useState<TrainingEvent[]>([])
  const [isFormOpen, setIsFormOpen] = useState(false)
  const [editingEvent, setEditingEvent] = useState<TrainingEvent | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    loadTrainingEvents()
  }, [])

  const loadTrainingEvents = () => {
    try {
      const savedEvents = localStorage.getItem('gym-training-events')
      if (savedEvents) {
        const parsed = JSON.parse(savedEvents)
        setTrainingEvents(parsed.map((event: any) => ({
          ...event,
          date: new Date(event.date),
          createdAt: new Date(event.createdAt)
        })))
      }
    } catch (error) {
      console.error('Error loading training events:', error)
      setTrainingEvents([])
    } finally {
      setIsLoading(false)
    }
  }

  const saveTrainingEvents = (events: TrainingEvent[]) => {
    try {
      localStorage.setItem('gym-training-events', JSON.stringify(events))
      setTrainingEvents(events)
    } catch (error) {
      console.error('Error saving training events:', error)
    }
  }

  const handleSaveEvent = (eventData: Omit<TrainingEvent, 'id' | 'createdAt'>) => {
    if (editingEvent) {
      // Update existing event
      const updatedEvents = trainingEvents.map(event =>
        event.id === editingEvent.id
          ? { ...event, ...eventData }
          : event
      )
      saveTrainingEvents(updatedEvents)
    } else {
      // Add new event
      const newEvent: TrainingEvent = {
        ...eventData,
        id: `event-${Date.now()}`,
        createdAt: new Date()
      }
      saveTrainingEvents([...trainingEvents, newEvent])
    }
    setEditingEvent(null)
  }

  const handleEditEvent = (event: TrainingEvent) => {
    setEditingEvent(event)
    setIsFormOpen(true)
  }

  const handleDeleteEvent = (eventId: string) => {
    if (confirm('Czy na pewno chcesz usun to wydarzenie?')) {
      const updatedEvents = trainingEvents.filter(event => event.id !== eventId)
      saveTrainingEvents(updatedEvents)
    }
  }

  const handleAddEvent = () => {
    setEditingEvent(null)
    setIsFormOpen(true)
  }

  const handleCloseForm = () => {
    setIsFormOpen(false)
    setEditingEvent(null)
  }

  const toggleEventCompletion = (eventId: string) => {
    const updatedEvents = trainingEvents.map(event =>
      event.id === eventId
        ? { ...event, completed: !event.completed }
        : event
    )
    saveTrainingEvents(updatedEvents)
  }

  // Get events for selected date
  const getEventsForDate = (date: Date) => {
    return trainingEvents.filter(event => 
      event.date.toDateString() === date.toDateString()
    )
  }

  // Get dates that have events
  const getDatesWithEvents = () => {
    return trainingEvents.map(event => event.date)
  }

  const selectedDateEvents = selectedDate ? getEventsForDate(selectedDate) : []

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <p className="text-gray-600">adowanie kalendarza...</p>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-black">Kalendarz Trening贸w</h1>
          <p className="text-gray-600 mt-2">
            Planuj swoje sesje treningowe i led藕 postpy
          </p>
        </div>
        <Button
          onClick={handleAddEvent}
          className="bg-black text-white hover:bg-gray-800"
        >
          Dodaj wydarzenie
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Calendar */}
        <div className="lg:col-span-2">
          <Card className="border-gray-200">
            <CardHeader>
              <CardTitle className="text-lg font-semibold text-black">
                Kalendarz
              </CardTitle>
              <CardDescription className="text-gray-600">
                Kliknij na dat, aby zobaczy zaplanowane treningi
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Calendar
                mode="single"
                selected={selectedDate}
                onSelect={setSelectedDate}
                className="rounded-md border border-gray-200"
                modifiers={{
                  hasEvent: getDatesWithEvents()
                }}
                modifiersStyles={{
                  hasEvent: {
                    backgroundColor: '#f3f4f6',
                    fontWeight: 'bold',
                    color: '#000'
                  }
                }}
              />
            </CardContent>
          </Card>
        </div>

        {/* Events for selected date */}
        <div>
          <Card className="border-gray-200">
            <CardHeader>
              <CardTitle className="text-lg font-semibold text-black">
                {selectedDate ? selectedDate.toLocaleDateString('pl-PL', {
                  weekday: 'long',
                  year: 'numeric',
                  month: 'long',
                  day: 'numeric'
                }) : 'Wybierz dat'}
              </CardTitle>
              <CardDescription className="text-gray-600">
                {selectedDateEvents.length > 0 
                  ? `${selectedDateEvents.length} zaplanowanych trening贸w`
                  : 'Brak zaplanowanych trening贸w'
                }
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-3">
              {selectedDateEvents.length === 0 ? (
                <div className="text-center py-6">
                  <p className="text-gray-500 mb-4">
                    Brak trening贸w na ten dzie
                  </p>
                  <Button
                    onClick={handleAddEvent}
                    variant="outline"
                    className="border-gray-300 text-black hover:bg-gray-50"
                  >
                    Dodaj trening
                  </Button>
                </div>
              ) : (
                selectedDateEvents.map((event) => (
                  <div
                    key={event.id}
                    className={`border rounded-lg p-3 ${
                      event.completed 
                        ? 'bg-green-50 border-green-200' 
                        : 'bg-white border-gray-200'
                    }`}
                  >
                    <div className="flex justify-between items-start mb-2">
                      <h4 className="font-medium text-black">
                        {event.exerciseName}
                      </h4>
                      <div className="flex space-x-1">
                        <Button
                          size="sm"
                          variant="ghost"
                          onClick={() => handleEditEvent(event)}
                          className="h-6 w-6 p-0 text-gray-500 hover:text-black"
                        >
                          锔
                        </Button>
                        <Button
                          size="sm"
                          variant="ghost"
                          onClick={() => handleDeleteEvent(event.id)}
                          className="h-6 w-6 p-0 text-gray-500 hover:text-red-600"
                        >
                          锔
                        </Button>
                      </div>
                    </div>
                    
                    {event.notes && (
                      <p className="text-sm text-gray-600 mb-2">
                        {event.notes}
                      </p>
                    )}
                    
                    <div className="flex items-center justify-between">
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => toggleEventCompletion(event.id)}
                        className={`text-xs ${
                          event.completed
                            ? 'border-green-300 text-green-700 bg-green-50'
                            : 'border-gray-300 text-gray-700'
                        }`}
                      >
                        {event.completed ? 'Ukoczone' : 'Oznacz jako ukoczone'}
                      </Button>
                    </div>
                  </div>
                ))
              )}
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Upcoming events summary */}
      <Card className="border-gray-200">
        <CardHeader>
          <CardTitle className="text-lg font-semibold text-black">
            Nadchodzce treningi
          </CardTitle>
        </CardHeader>
        <CardContent>
          {(() => {
            const upcomingEvents = trainingEvents
              .filter(event => event.date >= new Date() && !event.completed)
              .sort((a, b) => a.date.getTime() - b.date.getTime())
              .slice(0, 5)

            if (upcomingEvents.length === 0) {
              return (
                <p className="text-gray-500">Brak nadchodzcych trening贸w</p>
              )
            }

            return (
              <div className="space-y-2">
                {upcomingEvents.map((event) => (
                  <div key={event.id} className="flex justify-between items-center py-2 border-b border-gray-100 last:border-b-0">
                    <div>
                      <p className="font-medium text-black">{event.exerciseName}</p>
                      <p className="text-sm text-gray-600">
                        {event.date.toLocaleDateString('pl-PL')}
                      </p>
                    </div>
                    <Button
                      size="sm"
                      variant="ghost"
                      onClick={() => handleEditEvent(event)}
                      className="text-gray-500 hover:text-black"
                    >
                      Edytuj
                    </Button>
                  </div>
                ))}
              </div>
            )
          })()}
        </CardContent>
      </Card>

      <TrainingPlanForm
        isOpen={isFormOpen}
        onClose={handleCloseForm}
        onSave={handleSaveEvent}
        event={editingEvent}
        selectedDate={selectedDate}
      />
    </div>
  )
}
