"use client"

import { useState, useEffect } from "react"
import { useForm } from "react-hook-form"
import { TrainingEvent, Exercise } from "@/types"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

interface TrainingPlanFormData {
  date: string
  exerciseId: string
  notes?: string
}

interface TrainingPlanFormProps {
  isOpen: boolean
  onClose: () => void
  onSave: (event: Omit<TrainingEvent, 'id' | 'createdAt'>) => void
  event?: TrainingEvent | null
  selectedDate?: Date
}

export default function TrainingPlanForm({ 
  isOpen, 
  onClose, 
  onSave, 
  event, 
  selectedDate 
}: TrainingPlanFormProps) {
  const [exercises, setExercises] = useState<Exercise[]>([])
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [selectedExercise, setSelectedExercise] = useState<string>("")
  
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors }
  } = useForm<TrainingPlanFormData>()

  useEffect(() => {
    loadExercises()
  }, [])

  useEffect(() => {
    if (event) {
      reset({
        date: event.date.toISOString().split('T')[0],
        exerciseId: event.exerciseId,
        notes: event.notes || ""
      })
      setSelectedExercise(event.exerciseId)
    } else if (selectedDate) {
      reset({
        date: selectedDate.toISOString().split('T')[0],
        exerciseId: "",
        notes: ""
      })
      setSelectedExercise("")
    } else {
      reset({
        date: new Date().toISOString().split('T')[0],
        exerciseId: "",
        notes: ""
      })
      setSelectedExercise("")
    }
  }, [event, selectedDate, reset])

  const loadExercises = () => {
    try {
      const savedExercises = localStorage.getItem('gym-exercises')
      if (savedExercises) {
        const parsed = JSON.parse(savedExercises)
        setExercises(parsed.map((ex: any) => ({
          ...ex,
          createdAt: new Date(ex.createdAt),
          updatedAt: new Date(ex.updatedAt)
        })))
      }
    } catch (error) {
      console.error('Error loading exercises:', error)
      setExercises([])
    }
  }

  const onSubmit = async (data: TrainingPlanFormData) => {
    if (!selectedExercise) {
      return
    }

    setIsSubmitting(true)
    try {
      const selectedExerciseData = exercises.find(ex => ex.id === selectedExercise)
      
      await onSave({
        date: new Date(data.date),
        exerciseId: selectedExercise,
        exerciseName: selectedExerciseData?.name || "",
        notes: data.notes,
        completed: false
      })
      
      onClose()
      reset()
      setSelectedExercise("")
    } catch (error) {
      console.error("Error saving training event:", error)
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleClose = () => {
    onClose()
    reset()
    setSelectedExercise("")
  }

  const handleExerciseChange = (value: string) => {
    setSelectedExercise(value)
    setValue("exerciseId", value)
  }

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="text-xl font-semibold text-black">
            {event ? "Edytuj wydarzenie treningowe" : "Dodaj wydarzenie treningowe"}
          </DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="date" className="text-sm font-medium text-black">
              Data treningu
            </Label>
            <Input
              id="date"
              type="date"
              {...register("date", { required: "Data jest wymagana" })}
              className="border-gray-300"
            />
            {errors.date && (
              <p className="text-sm text-red-600">{errors.date.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="exercise" className="text-sm font-medium text-black">
              wiczenie
            </Label>
            <Select value={selectedExercise} onValueChange={handleExerciseChange}>
              <SelectTrigger className="border-gray-300">
                <SelectValue placeholder="Wybierz wiczenie" />
              </SelectTrigger>
              <SelectContent>
                {exercises.map((exercise) => (
                  <SelectItem key={exercise.id} value={exercise.id}>
                    {exercise.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            {!selectedExercise && (
              <p className="text-sm text-red-600">Wybierz wiczenie</p>
            )}
          </div>

          {selectedExercise && (
            <div className="bg-gray-50 rounded-lg p-3">
              <h4 className="font-medium text-black mb-2">Szczeg贸y wiczenia:</h4>
              {(() => {
                const exercise = exercises.find(ex => ex.id === selectedExercise)
                if (!exercise) return null
                
                return (
                  <div className="grid grid-cols-3 gap-2 text-sm">
                    <div className="text-center">
                      <p className="text-gray-500">Ci偶ar</p>
                      <p className="font-semibold">{exercise.maxWeight}kg</p>
                    </div>
                    <div className="text-center">
                      <p className="text-gray-500">Powt贸rzenia</p>
                      <p className="font-semibold">{exercise.reps}</p>
                    </div>
                    <div className="text-center">
                      <p className="text-gray-500">Serie</p>
                      <p className="font-semibold">{exercise.sets}</p>
                    </div>
                  </div>
                )
              })()}
            </div>
          )}

          <div className="space-y-2">
            <Label htmlFor="notes" className="text-sm font-medium text-black">
              Notatki (opcjonalne)
            </Label>
            <Textarea
              id="notes"
              {...register("notes")}
              placeholder="Dodatkowe informacje o treningu..."
              className="border-gray-300 resize-none"
              rows={3}
            />
          </div>

          <div className="flex space-x-3 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={handleClose}
              className="flex-1 border-gray-300 text-black hover:bg-gray-50"
              disabled={isSubmitting}
            >
              Anuluj
            </Button>
            <Button
              type="submit"
              className="flex-1 bg-black text-white hover:bg-gray-800"
              disabled={isSubmitting || !selectedExercise}
            >
              {isSubmitting ? "Zapisywanie..." : "Zapisz"}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
